<template>
  <div class="control" :class="{ controlPlaying: isPlaying }">
    <div class="control-btn control-btn-side" @click="gugugu">
      <i class="fa fa-question" />
    </div>
    <div class="control-btn control-btn-side" @click="handlePrev">
      <i class="fa fa-backward" />
    </div>
    <div class="control-btn" @click="handlePlay">
      <span class="play-btn" />
    </div>
    <div class="control-btn control-btn-side" @click="handleNext">
      <i class="fa fa-forward" />
    </div>
    <div
      class="control-btn control-btn-side control-btn-volume"
      @mousedown="adjust"
    >
      <div
        class="volume-control"
        :class="{ light: iconlight, dragging: dragging }"
      >
        <div
          class="volume-control-mask"
          :style="{ height: `${(1 - vol) * 150}px` }"
        ></div>
        <i
          class="fa"
          style="z-index: 1"
          :class="{ 'fa-volume-off': vol == 0, 'fa-volume-up': vol != 0 }"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { player } from "../player";
export default {
  data() {
    return {
      vol: 5 / 6,
      dy: 0,
      dragging: false,
      iconlight: false,
    };
  },
  computed: {
    ...mapState(["isPlaying"]),
  },
  methods: {
    randomMusic() {
      const req = new XMLHttpRequest();
      req.open(
        "GET",
        "https://api.i-meto.com/meting/api?server=netease&type=playlist&id=7336770281",
        true
      );
      req.responseType = "json";
      req.onload = () => {
        const data = req.response;
        for (let i = 0; i < data.length; i++) {
          const song = data[i];
          setTimeout(() => {
            player.appendOnlineFile(song.url, song.pic, song.title, song.author);
          }, 2000 * i);
        }
      };
      req.onerror = (err) => {
        console.log(err);
      };
      req.send();
    },
    handlePlay() {
      if (!player.isEmpty) {
        if (!this.isPlaying) {
          player.play();
        } else {
          player.pause();
        }
      }
    },
    handlePrev() {
      player.prev();
    },
    handleNext() {
      player.next();
    },
    adjust(evt) {
      this.dy = evt.clientY - (1 - this.vol) * 150;
      this.dragging = true;
      document.onmousemove = this.onmousemove;
      document.onmouseup = () => (this.dragging = false);
    },
    onmousemove(evt) {
      if (this.dragging) {
        var prog = 1 - (evt.clientY - this.dy) / 150;
        if (prog < 0) prog = 0;
        if (prog > 1) prog = 1;
        this.iconlight = prog < 0.12;
        this.vol = prog;
        player.GainNode.gain.value = prog * 1.2;
      }
    },
  },
  mounted() {
    this.randomMusic();
  },
};
</script>

<style>
.control {
  display: flex;
  height: 100%;
  user-select: none;
}
.control-btn {
  display: flex;
  margin: 2px;
  align-items: center;
  justify-content: center;
  flex: 1;
  border-radius: 6px;
  color: #ccc;
  font-size: 16px;
  transition: background-color 0.4s ease, transform ease 0.3s;
}
.control-btn:hover {
  background-color: #ddd;
  color: white;
  cursor: pointer;
}
.control-btn:active:not(.control-btn-volume) {
  background-color: #bbb;
  color: white;
}
.control-btn-side {
  font-size: 14px;
}
.play-btn {
  position: relative;
  width: 20px;
  height: 20px;
}
.play-btn::before {
  content: "";
  position: absolute;
  left: 10px;
  top: 50%;
  margin-left: -4px;
  margin-top: -10px;
  width: 0;
  height: 0;
  border: 12px solid transparent;
  border-left-color: #ccc;
  border-top-width: 10px;
  border-bottom-width: 10px;
  transition: all 0.2s ease;
}
.play-btn::after {
  content: "";
  position: absolute;
  right: 5px;
  top: 50%;
  margin-right: -4px;
  margin-top: -10px;
  width: 0;
  height: 20px;
  border: 0 solid transparent;
  border-width: 0 0 0 6px;
  border-left-color: #ccc;
  opacity: 0;
  transform: scale(0);
  transition: all 0.2s ease;
}
.controlPlaying .play-btn::before {
  border-width: 0 0 0 6px;
  height: 20px;
  left: 5px;
}
.controlPlaying .play-btn::after {
  opacity: 1;
  transform: scale(1);
}
.control-btn:hover .play-btn::before {
  border-left-color: white;
}
.control-btn:hover .play-btn::after {
  border-color: white;
}
.volume-control:hover,
.dragging {
  height: 150px !important;
  transform: translateY(0);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}
i {
  transition: transform ease 0.3s, color ease 0.2s;
}
.light:hover > i,
.light.dragging > i {
  color: white !important;
}
.volume-control:hover > i,
.dragging > i {
  transform: translateY(400%) scale(1.2);
  color: #ccc;
}
.volume-control {
  background-color: white;
  border-radius: 8px;
  transition: all ease 0.3s;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.volume-control-mask {
  position: absolute;
  align-self: baseline;
  width: 48px;
  transition: background-color ease-out 0.2s;
}
.volume-control:hover .volume-control-mask,
.dragging .volume-control-mask {
  background-color: rgba(0, 32, 100, 0.2);
}
</style>