class Dispatcher {
    constructor() {
        this.handlers = []
    }
    listen(handler) {
        this.handlers.push(handler)
    }
    emit(...args) {
        this.handlers.forEach(handler => handler(...args))
    }
}

class Player {
    constructor() {
        this.audioContext = new AudioContext()
        this.GainNode = this.audioContext.createGain()
        this.GainNode.connect(this.audioContext.destination)
        this.playList = []
        this.playIndex = 0

        this.emptyNode = {
            offset: 0,
            buffer: null
        }

        this.onPlay = new Dispatcher()
        this.onPause = new Dispatcher()
        this.onChange = new Dispatcher()
        this.onReady = new Dispatcher()
    }

    async readAudioBuffer(file) {
        return new Promise((res, rej) => {
            const reader = new FileReader()
            reader.onload = async evt => {
                this.audioContext
                    .decodeAudioData(evt.target.result)
                    .then(res, rej)
            }
            reader.onerror = rej
            reader.readAsArrayBuffer(file)
        })
    }

    async readOnlineAudioBuffer(url) {
        return new Promise((res, rej) => {
            const req = new XMLHttpRequest()
            req.open('GET', url, true)
            req.responseType = 'arraybuffer'
            req.onload = () => {
                const data = req.response
                this.audioContext
                    .decodeAudioData(data)
                    .then(res, rej)
            }
            req.onerror = rej
            req.send()
        })
    }

    async appendFile(file) {
        const image = new Image()
        image.src = require('./assets/cover1.jpg')
        this.playList.push({
            offset: 0,
            name: file.name,
            buffer: await this.readAudioBuffer(file),
            img: image.src
        })
        this.onReady.emit(this)
    }

    async appendOnlineFile(url, img, name, author = '', album = '') {
        const image = new Image()
        image.src = img
        this.playList.push({
            offset: 0,
            name,
            author,
            album,
            img: image.src,
            buffer: await this.readOnlineAudioBuffer(url),
        })
        console.log(this.playList);
    }

    play() {
        if (!this.playList.length || this.current.source) return
        const source = this.audioContext.createBufferSource()
        source.buffer = this.current.buffer
        source.onended = this.next.bind(this) // bind?
        source.connect(this.GainNode)
        source.start(0, this.current.offset)
        this.current.source = source
        this.current.start = this.audioContext.currentTime
        this.onPlay.emit(this)
    }

    pause() {
        if (!this.playList.length || !this.current.source) return
        this.current.source.stop(0)
        this.current.source.disconnect(0)
        this.current.source.onended = null
        this.current.source = null
        this.current.offset = this.position
        this.current.start = null

        this.onPause.emit(this)
    }

    stop() {
        this.pause()
        this.current.offset = 0
        this.current.start = null
    }

    next() {
        if (this.isEmpty) return
        this.stop()
        this.playIndex++
        if (this.playIndex >= this.playList.length) this.playIndex = 0
        this.play()
        this.onChange.emit(this)
    }

    prev() {
        if (this.isEmpty) return
        this.stop()
        this.playIndex--
        if (this.playIndex < 0)
            this.playIndex = this.playList.length ? this.playList.length - 1 : 0
        this.play()
        this.onChange.emit(this)
    }

    get isEmpty() {
        return this.playList === this.emptyNode
    }

    get current() {
        return this.playList[this.playIndex] || this.emptyNode
    }

    get position() {
        return !this.playList.length ? 0 : this.current.offset +
            (this.current.start === null ? 0 : this.audioContext.currentTime - this.current.start)
    }

    set position(val) {
        if (!this.playList.length) return
        this.stop()
        this.current.offset = val
        this.current.start = null
        this.play()
    }

    get duration() {
        return this.current.buffer ? this.current.buffer.duration : 0
    }
}

export const player = new Player();