<template>
  <div class="disk" :class="{ diskPlaying: isPlaying }">
    <label
      for="file"
      class="disk_cover"
      ref="cover"
      :style="{
        transform: stopMatrix,
        backgroundImage: imgurl ? `url(${imgurl})` : '',
      }"
      :class="{ busy: loading }"
    ></label>
    <input
      id="file"
      ref="file"
      type="file"
      accept=".mp3"
      multiple
      @change="handleChange"
    />
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import { player } from "../player";
export default {
  data() {
    return {
      stopMatrix: "",
      loading: false,
    };
  },
  computed: {
    ...mapState(["isPlaying", "imgurl"]),
  },
  watch: {
    isPlaying(val) {
      if (val) {
        const [, sin, cos] = this.stopMatrix.match(
          /^matrix\(([^,]+),([^,]+)/
        ) || [0, 0, 0];
        const deg = ((Math.atan2(cos, sin) / (2 * Math.PI)) * 360) % 360;
        const styles = [...document.styleSheets];
        styles.forEach((style) => {
          const rules = [...style.cssRules];
          rules.forEach((rule) => {
            if (rule.type === rule.KEYFRAMES_RULE && rule.name === "rotate") {
              rule.cssRules[0].style.transform = `rotate(${deg}deg)`;
              rule.cssRules[1].style.transform = `rotate(${deg + 360}deg)`;
            }
          });
        });
      } else
        this.stopMatrix = window.getComputedStyle(this.$refs.cover).transform;
    },
  },
  methods: {
    ...mapMutations(["togglePlay", "changeCover"]),
    async handleChange() {
      const target = this.$refs.file;
      const files = target.files || [];

      for (let i = 0; i < files.length; i++) {
        this.loading = true;
        await player.appendFile(files[i]);
        this.loading = false;
      }

      target.value = "";
    },
  },
  mounted() {
    player.onReady.listen(() => {
      this.changeCover();
    });
    player.onChange.listen(() => {
      this.changeCover();
    });
    player.onPlay.listen(() => {
      this.togglePlay(true);
    });
    player.onPause.listen(() => {
      this.togglePlay(false);
    });
  },
};
</script>

<style>
.disk {
  position: relative;
  padding-top: 100%;
  border-radius: 100%;
  overflow: hidden;
  transform: translateY(-50%) scale(0.88);
  transform-origin: center bottom;
  transition: all 0.6s ease;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.4);
}
.disk_cover {
  cursor: pointer;
}
.busy {
  cursor: wait;
}
.disk input {
  display: none;
}
.disk_cover {
  position: absolute;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  transition: all 0.3s ease;
  background-image: radial-gradient(circle, #444 0%, #333 100%);
  background-size: cover;
  background-position: center;
}
.disk_cover::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -8px;
  margin-top: -8px;
  width: 16px;
  height: 16px;
  border-radius: 100%;
  background-image: linear-gradient(45deg, white, #dabad1);
  box-shadow: 0 1px 1px 1px rgba(0, 0, 0, 0.2);
}
.diskPlaying {
  transform: translateY(-50%);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1),
    0 20px 20px -10px rgba(108, 29, 171, 0.3);
}
.diskPlaying .disk_cover {
  animation: rotate infinite linear 12s;
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>