<template>
  <div class="progress" :class="{ progressPlaying: isPlaying }">
    <div class="progress-title-container">
      <h2 class="progress-title" :class="{ scroll: ScrollTitle }">
        {{ formatedName }}
        <h3 class="progress-author">{{ author }}</h3>
      </h2>
    </div>
    <div class="progress-text">
      {{ formatedPosition }} / {{ formatedDuration }} {{ album }}
    </div>
    <div class="progress-line" @mousedown="adjust" ref="progress">
      <span :style="{ width: progress }"></span>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { player } from "../player";

function formatTime(val) {
  const min = ~~(val / 60);
  const sec = ~~(val % 60);
  return `${min < 10 ? "0" : ""}${min}:${sec < 10 ? "0" : ""}${sec}`;
}

export default {
  data() {
    return {
      name: "",
      position: 100,
      duration: 3,
      progress: "",
      dragging: false,
      dx: 0,
      offsetx: 0,
      ScrollTitle: false,
    };
  },
  computed: {
    ...mapState(["isPlaying", "LoadingText"]),
    formatedName() {
      return this.name?.replace(/\.mp3$/, "");
    },
    formatedPosition() {
      return formatTime(this.position);
    },
    formatedDuration() {
      return formatTime(this.duration);
    },
    author() {
      return player.current.author ?? "";
    },
    album() {
      return player.current.album ?? "";
    },
  },
  methods: {
    adjust(evt) {
      this.dx = evt.clientX - evt.layerX;
      this.dragging = true;
      const prog = evt.layerX / this.$refs.progress.clientWidth;
      this.position = prog * player.duration;
      this.progress = `${prog * 100}%`;
      document.onmousemove = this.onmousemove;
      document.onmouseup = this.onmouseup;
    },
    onmousemove(evt) {
      if (this.dragging) {
        var prog = (evt.clientX - this.dx) / this.$refs.progress.clientWidth;
        if (prog < 0) prog = 0;
        if (prog > 1) prog = 1;
        this.progress = `${prog * 100}%`;
      }
    },
    onmouseup() {
      if (this.dragging) {
        this.progress = `${(this.position / this.duration) * 100}%`;
        player.position = this.position;
        this.dragging = false;
      }
    },
  },
  mounted() {
    const draw = () => {
      requestAnimationFrame(draw);
      if (!this.dragging) {
        this.progress = `${(player.position / player.duration) * 100}%`;
        this.position = player.position;
      } else {
        this.position = (parseFloat(this.progress) / 100) * this.duration;
      }
      this.duration = player.duration;
      this.name = player.current.name ?? this.LoadingText;

      const node = document.getElementsByTagName("h2")[0];
      this.ScrollTitle = node.clientWidth > node.parentNode.clientWidth;
    };
    draw();
  },
};
</script>

<style>
.progress {
  padding-left: 110px;
  padding-right: 6px;
  height: 100%;
  border-radius: 6px 6px 0 0;
  background-color: rgba(255, 255, 255, 0.5);
  transition: all ease 0.6s;
  user-select: none;
}
.progressPlaying {
  transform: translateY(-100%);
}
.progress-title-container {
  overflow: hidden;
}

.progress-title {
  padding-top: 2px;
  font-size: 18px;
  transform: translateY(8%);
  font-weight: bold;
  white-space: nowrap;
  display: inline-block;
}
.progress-author {
  padding-top: 4px;
  padding-left: 10px;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.5);
  font-weight: normal;
  white-space: nowrap;
  display: inline-block;
}
.scroll {
  animation: 12s wordsLoop ease-in-out infinite alternate;
}
@keyframes wordsLoop {
  10% {
    transform: translateX(0px);
  }
  90% {
    transform: translateX(calc(262px - 100%));
  }
  100% {
    transform: translateX(calc(262px - 100%));
  }
}
.progress-text {
  padding-left: 1px;
  font-size: 12px;
  font-weight: bold;
  color: #ccc;
  transform: translateY(15%) scale(0.6);
  transform-origin: left top;
  white-space: nowrap;
}
.progress-line {
  margin-left: 1px;
  height: 3px;
  border-radius: 3px;
  overflow: hidden;
  transform: translateY(-50%);
  background-color: #ddd;
}
.progress-line:hover {
  cursor: ew-resize;
}
.progress-line span {
  display: block;
  height: 100%;
  background-color: #ec51a5;
}
</style>